@use 'global' as *;

.card {
	display: flex;
	flex-direction: column;
	padding: my-rem(18) my-rem(21) my-rem(24);
	background-color: $base2;
	box-shadow: 2px 2px 10px 0px rgba(0, 0, 0, 0.25);
	margin-right: auto;
	margin-left: auto;
	border-radius: my-rem(20);

	@include mq(md) {
		padding: my-rem(18) my-rem(16) my-rem(24);
	}
}

.card a:hover {
	opacity: 0.5;
	transition: 0.3s;
}

.card__image {
	position: relative;
	border-radius: my-rem(10);
}
.card__image--sub {
	width: calc((121 / 246) * 100%);
}
.card__image::before {
	content: '';
	display: block;
	padding-top: calc((201 / 301) * 100%);
}
.card__image--sub::before {
	padding-top: calc((91 / 121) * 100%);
}

.card__image--hover {
	overflow: hidden;
}
.card__image--hover:hover img {
	position: absolute;
	top: 0;
	left: 0;
	transform: scale(1.3);
}

.card__image img {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
	height: 100%;
	object-fit: cover;
}
.card__body {
	margin-top: my-rem(16);
	background-color: $base2;
}
.card__body.card__body--sub {
	margin-left: my-rem(20);
	margin-top: initial;
	width: calc((125 / 246) * 100%);
	justify-content: flex-start;
}
.card__header {
	height: 100%;
	padding-bottom: my-rem(2);
	border-bottom: my-rem(1) solid $brown;
	@include mq(md) {
		padding-bottom: my-rem(4);
	}
}
.card__date {
	font-family: $en-font-family1;
	font-weight: $regular;
	font-size: my-rem(12);
	line-height: calc(18 / 12);
	color: $dark-green;
}

.card__title {
	font-size: my-rem(16);
	font-family: $jp-font-family1;
	font-weight: $medium;
	line-height: calc(24 / 16);
}

.card__meta {
	margin-top: my-rem(16);
}
